@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Roboto&family=Share+Tech+Mono&display=swap');

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

a {
    text-underline-offset: 4px;
}

.absolute {
  position: absolute;
  top: 0;
  left: 0;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.h-min {
  min-height: 100%;
}

.page {
    position: relative;
    width: 100%;
    font-family: 'Roboto', sans-serif;
    color: white;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    padding: 20px;
}

.page-child {
    position: absolute;
    width: 720px;
    height: 1080px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    overflow: visible;
}

h1 {
    margin-top: 0;
    margin-bottom: 20px;
    padding: 0;
    font-family: 'Heebo', sans-serif;
    font-size: 100px;
    line-height: 130px;
    text-align: left;
}

h3 {
  margin-top: 0;
}

svg {
  vertical-align: bottom;
}

.p-10 {
  padding: 20px;
}

.job-title {
  padding: 28px;
  border: 2px solid white;
  font-family: 'Share Tech Mono', monospace;
  font-size: 25px;
  font-weight: 100;
  letter-spacing: 4px;
  margin-bottom: 50px;
  border-radius: 10px;
  text-align: center;
}

.job-type {
  font-family: 'Share Tech Mono', monospace;
  font-size: 25px;
  font-weight: 100;
  line-height: 40px;
  margin-bottom: 0;
}

.work-title {
  max-width: 400px;
  padding: 28px;
  border: 2px solid white;
  font-family: 'Share Tech Mono', monospace;
  font-size: 25px;
  font-weight: 100;
  letter-spacing: 4px;
  margin: 0 auto 50px auto;
  border-radius: 10px;
  text-align: center;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.work-title.visible {
  opacity: 1;
  transform: translateY(0);
}

.work-type {
    font-family: 'Share Tech Mono', monospace;
    margin-top: 30px;
    font-size: 22px;
    font-weight: 100;
    line-height: 40px;
    margin-bottom: 0;
}

.experiences {
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  justify-content: center;
}

.experiences div {
  flex: 0;
}

.experience {
  color: white;
  font-family: 'Share Tech Mono', monospace;
  font-size: 22px;
  font-weight: 100;
  flex: 0 0 50%;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.experience.visible {
  opacity: 1;
  transform: translateY(0);
}

.experience .type {
  position: relative;
  display: inline-block;
  flex-grow: 0;
  border-radius: 50%;
  padding: 10px;
  border: 2px solid white;
}

.experience .type img {
  width: 30px;
  height: 30px;
}

.experience .year {
  white-space: pre;
  margin-right: 15px;
  opacity: 0.7;
  font-size: 19px;
  line-height: 25px;
}

.experience .connector {
  border-left: 4px dotted white;
  margin-left: 25px;
  padding-top: 80px;
}

.experience .description {
  display: flex;
}

.experience .details {
  margin-left: 15px;
}

.experience .details .title {
  display: inline-block;
  margin-bottom: 5px;
}
.experience .details .location {
  white-space: pre;
  margin-bottom: 5px;
  transition: all 0.1s ease-out;
}

.experience .details .location a {
  color: white !important;
  transition: all 0.1s ease-out;
}

.experience .details .location:hover {
  transform: translateX(5px);
  opacity: 0.7;
}

.experience .details .length {
  white-space: pre;
  opacity: 0.7;
}


.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: -5px;
  font-family: 'Share Tech Mono', monospace;
}

.pagination button {
  cursor: pointer;
  background: none;
  border: none;
  color: white;
  font-size: 35px;
  line-height: 35px;
  outline: none;
}

.pagination button:hover {
  opacity: 0.5;
}

.projects-list {
  list-style: none;
  padding: 0;
  opacity: 0;
  transform: translateX(-100%);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.projects-list.active {
  opacity: 1;
  transform: none;
}

.project-title {
  width: 100%;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.project-title.visible {
  opacity: 1;
  transform: translateY(0);
}

.project-title button {
  text-align: left;
  width: 100%;
  font-family: 'Share Tech Mono', monospace;
  color: white;
  font-size: 22px;
  background-color: #22222222;
  border-radius: 50px;
  padding: 20px 25px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-out;
  outline: none;
  pointer-events: none;
}

.project-title button:focus,
.project-title button:active,
.project-title button:hover {
  background-color: #ffffff55;
  transform: translate(5px, 0);
}

.projects-list.active .project-title button {
  pointer-events: auto;
}

.projects-list li {
  margin-bottom: 20px;
}

.projects-list li:last-child {
  margin-bottom: 0;
}

.projects-container {
  padding-top: 30px;
}

.projects {
  max-width: 400px;
  margin: 0 auto;
  position: relative;
}

.project {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  flex-direction: column;
  font-family: 'Share Tech Mono', monospace;
  font-size: 20px;
  font-weight: 100;
  text-align: left;
  pointer-events: none;
  opacity: 0;
  transform: translateX(100%);
  padding: 0 10px;
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.project.active {
  pointer-events: auto;
  opacity: 1;
  transform: none;
}

.project p {
  margin: 0;
  margin-bottom: 15px;
}

.project p:last-child {
  margin-bottom: 0;
}

.project button {
  display: inline-block;
  margin-top: 20px;
  margin-right: auto;
  font-family: 'Share Tech Mono', monospace;
  color: white;
  font-size: 22px;
  background-color: #22222222;
  border-radius: 50px;
  padding: 20px 40px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-out;
  outline: none;
}

.project button:focus,
.project button:active,
.project button:hover {
  background-color: #ffffff55;
  transform: translate(5px, 0);
}

.stack {
  opacity: 0.7;
  margin-top: 25px;
}

.stack .tool {
  font-size: 18px;
  line-height: 30px;
  margin-right: 10px;
}

.stack .tool:last-child {
  margin-right: 0;
}

.skill-container {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.skill-container.visible {
  opacity: 1;
  transform: translateY(0);
}

.skill {
  font-family: 'Share Tech Mono', monospace;
  font-weight: 300;
  margin: 0 10px 20px 10px;
  text-align: center;
  border-radius: 20px;
  display: inline-block;
  background-color: #22222222;
  border-radius: 50px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.2s ease-out;
}

.skill:hover {
  background-color: #ffffff55;
  transform: translateY(-5px);
}

.skills {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  white-space: pre;
  text-align: left;
  width: 100%;
  justify-content: center;
}

.skills.centered {
  justify-content: center !important;
}

.skills-column {
    padding-left: 20px;
    padding-right: 20px;
}

img {
    vertical-align: bottom;
}

.canvas {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card {
  width: 100%;
  opacity: 0;
  position: relative;
  padding: 60px 60px 70px 60px;
  animation-name: fade-in-anim;
  animation-duration: 0.7s;
  animation-delay: 0.1s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

.letter:hover {
    opacity: 0.5;
    transform: translateY(-10px);
}

.contact-me {
  max-width: 400px;
  margin: 0 auto;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.contact-me.visible {
  opacity: 1;
  transform: translateY(0);
}

.contact-me * {
  display: flex;
  justify-content: center;
  color: white !important;
}

.contact-me a {
  margin-top: 70px;
  font-family: 'Share Tech Mono', monospace;
  font-size: 22px;
  background-color: #22222222;
  border-radius: 50px;
  padding: 25px 35px;
  transition: all 0.2s ease-out;
  opacity: 1 !important;
}

.contact-me a:hover {
  background-color: #ffffff55;
  transform: translateY(-5px);
}

@media only screen and (max-width: 1024px) {}

.background-gradient {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
}

.background-animated {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 1s;
    will-change: transform;
}

.background-animated.hidden {
  opacity: 0.5;
}

@media (max-aspect-ratio: 4/3) {
    .background-animated {
        object-fit: cover;
    }
  }

.fade-in {
    opacity: 0;
    animation: fade-in-anim;
    animation-duration: 1s;
    animation-delay: 0.25s;
    animation-fill-mode: forwards;
}

@keyframes fade-in-anim {
    0% {
        opacity: 0;
        transform: translateY(25px);
    }

    50% {
        transform: translateY(-5px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.scroll-for-more {
    position: fixed;
    bottom: 0;
    left: 50%;
    font-family: 'Share Tech Mono', monospace;
    font-size: 20px;
    font-weight: bold;
    color: white;
    transform: translateX(-50%);
    padding-bottom: 20px;
    transition: all 0.5s ease-in-out;
    opacity: 0;
}

.path {
    stroke-dasharray: 50;
    animation: dash 30s linear;
    animation-iteration-count: infinite;
}

@keyframes dash {
    to {
        stroke-dashoffset: 1000;
    }
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #CCC; 
}